.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    background: -webkit-gradient(linear, left top, left bottom, from(#6dd5ed), to(#2193b0));
    background: -webkit-linear-gradient(top, #6dd5ed, #2193b0);
    background: -o-linear-gradient(top, #6dd5ed, #2193b0);
    background: linear-gradient(to bottom, #6dd5ed, #2193b0);
}


