.toast {
    display: block;
    position: fixed;
    padding: 8px 16px;
    left: 50%;
    bottom: 16px;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #2193b0;
    background-color: #fff;
    -webkit-box-shadow: 0 0 8px #fff;
            box-shadow: 0 0 8px #fff;
}

.animation {
    -webkit-animation: fade 3s;
            animation: fade 3s;
}

@-webkit-keyframes fade {
    from {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fade {
    from {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}


