.todo {
    padding: .5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.todo > .content {
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0 .5em;
}

.line-through {
    text-decoration: line-through;
}

.delete {
    position: relative;
    top: -2px;
    font-size: 22px !important;
}

.delete:hover {
    cursor: pointer;
    opacity: .7;
}

.todoFadeIn {
    -webkit-animation: todoFadeIn 1s;
            animation: todoFadeIn 1s;
}

@-webkit-keyframes todoFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes todoFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


